import React from 'react';
import {useTranslation} from "react-i18next";

import img from '../../assets/image/coin-game-img.png'
import './CoinGame.css'

const CoinGame = () => {
    const { t } = useTranslation()
    return (
        <section id={'game'}>
            <div className="container">
                <span>{t("coinGame.spanText")}</span>
                <h3 className={'section__title'}>{t("coinGame.title")}</h3>
                <p className={'game__text'}>{t("coinGame.first")}</p>
            </div>
            <div className={'game__row'}>
                <div className={'container'}>
                    <div className="game__left">
                        <p>{t("coinGame.second")}</p>
                        <p>{t("coinGame.third")}</p>
                    </div>
                    <div className="game__right">
                        <img src={img} alt="coin game"/>
                    </div>
                </div>
            </div>
            <div className={'disclaimer'}>
                <div className={'disclaimer__title'}>
                    <h4>{t("disclaimer.title")}</h4>
                </div>
                <div className="container">
                    <p>{t("disclaimer.text")}</p>
                </div>
            </div>
        </section>
    );
};

export default CoinGame;