import React from 'react';
import {useTranslation} from "react-i18next";

import './FeedBackForm.css'

const FeedBackForm = () => {
    const { t } = useTranslation()
    return (
        <form className={'feedback__form'}>
            <input type="text" placeholder={t("feedback.form.name")}/>
            <input type="text" placeholder={t("feedback.form.number")}/>
            <textarea placeholder={t("feedback.form.message")}/>
            <button type={"submit"}>{t("feedback.form.button")}</button>
        </form>
    );
};

export default FeedBackForm;