import React from 'react';
import {useTranslation} from "react-i18next";

import img from '../../assets/image/how-buy-img.png'
import './HowToBuy.css'
const HowToBuy = () => {
    const { t } = useTranslation()

    const firstList = t('howBuy.firstList', {returnObjects: true})
    const secondList = t('howBuy.secondList', {returnObjects: true})

    return (
        <section id={'coin-buy'}>
            <div className="container">
               <h3 className={'section__title'}>{t("howBuy.title")}</h3>
                <div className={'coin-buy__row'}>
                    <div className={'coin-buy__box'}>
                        <h4>1</h4>
                        <ul>
                            {
                                firstList?.map((item) => <li key={ item.id }>{ item.text }</li>)
                            }
                        </ul>
                    </div>
                    <div className={'coin-buy__box'}>
                        <h4>2</h4>
                        <ul>
                            { secondList?.map((item) => <li key={ item.id }>{ item.text }</li>) }
                        </ul>
                    </div>
                    <div className={'coin-buy__box'}>
                        <h4>3</h4>
                        <div className={'coin-buy__box-token'}>
                            <span>{t("howBuy.spanText")}</span>
                            <p>0x82fEd503954f26C2a09F0E8c6e5EC784aCd25AcE</p>
                        </div>
                    </div>
                </div>
            </div>
            <img className={'coin-buy__img'} src={img} alt="how buy"/>
        </section>
    );
};

export default HowToBuy;