import React from 'react';
import {useTranslation} from "react-i18next";

import './Footer.css'
const Footer = () => {
    const { t } = useTranslation()
    return (
        <footer>
            <div className="container">
                <p>{t("footer.cookie")}</p>
                <p>{t("footer.privacy")}</p>
                <p> AltynCoin 2022</p>
            </div>
        </footer>
    );
};

export default Footer;