import React from 'react';
import {useTranslation} from "react-i18next";
import HeaderNavItem from "./HeaderNavItem";

import roadMap from "../../../assets/pdf/RoadMap.pdf"
import whitePaper from "../../../assets/pdf/whitePaper.pdf"
import './HeaderNav.css'
const HeaderNav = ({isVisible, setIsVisible}) => {
    const { t } = useTranslation()

    const headerNav = t('headerNav', {returnObjects: true})

    return (
        <div className={isVisible ? 'nav__container': 'nav__container nav__container__hide'}>
            <ul className={isVisible ? 'nav-list' : 'nav-list nav-list__hide'}>
                {headerNav.map((item) => {
                    return <HeaderNavItem
                        key={item.id}
                        title={item.title}
                        path={item.path}
                        setIsVisible={setIsVisible}
                        isVisible={isVisible}
                    />
                })}
                <li>
                    <a href={roadMap} target={"_blank"} rel="noreferrer">{t("headerNavPdf.first")}</a>
                </li>
                <li>
                    <a href={whitePaper} target={"_blank"} rel="noreferrer">{t("headerNavPdf.second")}</a>
                </li>
                <div
                    className={"close-btn"}
                    onClick={() => setIsVisible(!isVisible)}
                >
                    <span></span>
                </div>
            </ul>
            <div className={'back-drop'} onClick={() => setIsVisible(!isVisible)}/>
        </div>
    );
};

export default HeaderNav;