import React from 'react';
import {useTranslation} from "react-i18next";

import img from '../../assets/image/after-buy-img.png'
import './AfterBuy.css'
const AfterBuy = () => {
    const { t } = useTranslation()

    return (
        <section id={'after-buy'}>
            <div className="container">
                <h3 className={'section__title'}>{t("afterBuy.title")}</h3>
                <div className="after-buy__row">
                    <div className="after-buy__box">
                        <p>{t("afterBuy.text")}</p>
                    </div>
                    <div className="after-buy__box">
                        <img src={img} alt="after buy"/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default AfterBuy;