import React from 'react';
import {useTranslation} from "react-i18next";

import diagram from '../../assets/image/diagram.png'
import mobileDiagram from '../../assets/image/mobile-diagram.png'
import './Tocenomika.css'
const Tocenomika = () => {
    const { t } = useTranslation()

    return (
        <section id={'tocenomika'}>
            <div className="container">
                <h3 className={'section__title'}>Tocenomika</h3>
                <p>
                    {t("teconomika.text.first")} <br/>
                    {t("teconomika.text.second")}
                </p>
                <div className={'tocenomika__box'}>
                    <img className={'mob-diagram'} src={mobileDiagram} alt="mobile-diagram"/>
                    <div className={'parent-box'}>
                        <img src={diagram} alt="diagram"/>
                        <span className={'first-card'}>{t("teconomika.first")}</span>
                        <span className={'second-card'}>{t("teconomika.second")}</span>
                        <span className={'third-card'}>{t("teconomika.third")}</span>
                        <span className={'fourth-card'}>{t("teconomika.fourth")}</span>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Tocenomika;